.Create .title p {
    text-align: center;
    font-family: sans-serif;
    font-weight: 800;
    font-size: xx-large;
    color: red;

}

.tableform {
    margin-left: 30%;
    align-items: center;
    position: absolute;
    margin-right: 20%;
    padding: 5%;
    background-color: black;
    border-radius: 20px;
}

.tableformdata {
    margin-left: auto;
    margin-right: auto;
}

.tableform table label {
    margin-left: 15px;
    display: inline;
    color: red;
}

.tableform table input {
    margin-bottom: 15px;
    margin-left: 15px;
    border-radius: 2rem;
    padding: 10px;
}

.tableform button {
    margin-left: 70%;
    border-radius: 2rem;
    color: black;
    width: 150px;
    height: 40px;
    margin-top: 10px;
}

.tableform button:hover {
    background-color: green;
}


.tableform .message {
    text-align: center;
    display: inline;
    font-size: 1em;
    color: #36a936;
    
}
