.heading H1 {
    color: red;
    text-align: center;
}

.heading .headingButton  button {
    text-align: center;
    display: block;
    margin: auto;
    border-radius: 2rem;
    padding: 5px;
    color: brown;
    background-color: white;
    border-style: none;
}

.heading .headingButton button {
    transition-duration: 0.4s;
}

.heading .headingButton button:hover {
    background-color: azure;
    text-decoration: none;
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.heading .headingButton button:active {
    background-color: #3e8e41;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.tableclass table {
    max-height: 300px;
    border-collapse: collapse;
    margin: 25px 0;
    font-size: 0.9em;
    font-family: sans-serif;
    min-width: 400px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    margin-left: auto;
    margin-right: auto;
    

}

.tableclass {
    max-height: 300px;
    overflow-y: auto;
    scrollbar-width: thin; /* Firefox */
    -ms-overflow-style: none; /* IE and Edge */
}
.tableclass::-webkit-scrollbar {
    width: 0em;
  }
  
  .tableclass::-webkit-scrollbar-thumb {
    background-color: #888;
  }

.tableclass thead tr {
    background-color: #009879;
    color: #ffffff;
    text-align: center;
}

.tableclass tbody tr {
    margin-top: 2px;
    text-align: center;
}

.tableclass th,
.tableclass td {
    padding: 12px 15px;
}

.tableclass tbody tr {
    border-bottom: 1px solid #dddddd;
}

.tableclass tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

.tableclass tbody tr:last-of-type {
    border-bottom: 2px solid #009879;
}


.tableclass tbody tr.active-row {
    font-weight: bold;
    color: #009879;
}


.heading .tableclass .deleted button{
  border-radius: 5rem; 
  border-color: red;
  font-variant: inherit;
}

.reloadMessage{
    padding-top: 5px;
    color: red;
}