.update .heading {
    text-align: center;
    font-family: sans-serif;
    font-weight: 800;
    font-size: xx-large;
    color: red;
}

.update-form {

    background-color: black;
    padding: 115px;
    position: absolute;
    border-radius: 15px;
    margin-left: 31%;
}

.update-table-form label {
    margin-left: 2px;
    display: inline;
    color: red;

}

.update-table-form input {
    margin-bottom: 10px;
    margin-left: 10px;
    border-radius: 2rem;
    padding: 10px;
}


.update-table-form button {
    margin-left: 80%;
    border-radius: 2rem;
    color: black;
    width: 100px;
    height: 40px;
    margin-top: 25px;
}

.update-table-form button:hover {
    background-color: green;
}

.update-form .message {
    text-align: center;
    display: inline;
    font-size: 1em;
    color: #36a936;
    
}