ul {
    list-style-type: none;
    border-radius: 10px;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}

li {
    float: left;
}

li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}


li a:hover {
    background-color: #0fc136;
}


.rightclass {
    float: right;
}

.active {
    background-color: #aa0420;
}